/*
**文档样式**
*/
.page-content {
  display: flex;
}
.page-content .article {
  width: calc(100% - 200px);
}
.page-content .article h1 {
  font-family: Chinese Quote, -apple-system, BlinkMacSystemFont, Segoe UI, Roboto, PingFang SC, Hiragino Sans GB, Microsoft YaHei, Helvetica Neue, Helvetica, Arial, sans-serif;
  font-size: 36px;
  line-height: 1.389;
  font-weight: 700;
  color: #262626;
  display: inline;
}
.page-content .article h2 {
  font-size: 28px;
  line-height: 36px;
  margin: 26px 0 10px 0;
  white-space: normal;
  word-spacing: 1px;
  color: #262626;
  font-weight: bold;
  padding: 0;
}
.page-content .article h3 {
  font-size: 24px;
  line-height: 32px;
  margin: 21px 0 5px 0;
  white-space: normal;
  word-spacing: 1px;
  color: #262626;
  font-weight: bold;
  padding: 0;
}
.page-content .article .tip {
  width: 100%;
  border-left: 5px solid #94db9a;
  background-color: #dcf8dc;
  /*background: linear-gradient(217deg, #FFFFCC, rgba(255, 255, 0, 0) 70.71%),
                linear-gradient(127deg, #CCFFFF, rgba(0, 0, 255, 0) 70.71%),
                linear-gradient(336deg, #FFCCCC, rgba(255, 0, 0, 0) 70.71%);*/
  padding: 10px 0;
}
.page-content .article .tip .tip-icon {
  margin: 0 15px 0 5px;
}
.page-content .article .tip .tip-title {
  font-weight: 600;
  color: #000;
}
.page-content .article .tip .tip-content {
  padding-left: 20px;
}
.page-content .article .tip .tip-content em {
  color: #595959;
}
.page-content .article .code-box {
  margin-bottom: 10px;
  border: 5px solid #ddd;
  transition: border 0.2s;
}
.page-content .article .code-box:hover {
  border-color: #999;
}
.page-content .article .code-box-active {
  border-color: #94db9a;
}
.page-content .article .code-box-active:hover {
  border-color: #94db9a;
}
.page-content .article .text-center {
  text-align: center;
}
.page-content .article .text-right {
  text-align: right;
}
.page-content .article .indent-2 {
  text-indent: 2em;
}
.page-content .article .anticon {
  text-indent: 0;
  font-size: 32px;
}
.page-content .article em {
  color: #8c8c8c;
}
.page-content .article .diagram {
  width: 100%;
  max-width: 300px;
  height: 300px;
  border: 1px solid #666;
  margin: 20px auto;
}
.page-content .article .article-title {
  margin-bottom: 32px;
  text-align: center;
}
.page-content .article .article-content {
  line-height: 1.74;
  font-size: 16px;
  color: #404040;
  letter-spacing: 0.008em;
}
.page-content .article .article-content p {
  white-space: normal;
  margin-bottom: 8px;
}
.page-content .article .article-content ol {
  list-style-type: decimal;
  margin: 0 0 0 3px;
  padding: 0;
}
.page-content .article .article-content ol li {
  white-space: normal;
  margin-left: 23px;
}
.page-content .article .article-content ol[data-lake-indent="2"] {
  padding-left: 4em;
  list-style-type: lower-roman;
}
.page-content .article .article-content img {
  margin: 10px 0;
  width: auto;
  max-width: 100%;
}
.page-content .article .article-content ul {
  list-style-type: disc;
  margin: 0 0 0 3px;
  padding: 0;
}
.page-content .article .article-content ul li {
  white-space: normal;
  margin-left: 23px;
}
.page-content .article .article-content ul[data-lake-indent="1"] {
  padding-left: 2em;
  list-style-type: circle;
}
.page-content .article .article-content ul[data-lake-indent="2"] {
  padding-left: 4em;
  list-style-type: square;
}
.page-content .article .article-content a {
  word-wrap: break-word;
  touch-action: manipulation;
}
.page-content .article .article-content .CodeMirror {
  max-width: 800px;
  margin: 0 auto;
}
.page-content .article .article-content .ant-list .ant-card .ant-card-cover {
  padding: 1px;
}
.page-content .article .article-content .ant-list .ant-card img {
  margin: 0;
}
.page-content .article .article-content .ant-list .ant-card .ant-card-body {
  padding: 10px;
}
.page-content .article .article-content .ant-list .ant-card:hover {
  transform: scale(1.08);
}
.page-content .article .article-content .ant-list .ant-card-hoverable:hover .ant-card-meta-title {
  color: #1890ff;
}
